
import React, {Component} from 'react';
import axios from 'axios';
import ButtonHome from '../partials/ButtonHome';

import { PieChart, Pie,Cell, LabelList,Legend, Tooltip } from "recharts";

let api = 'https://mercadosefectivos-bi-api.herokuapp.com';

export default class ViewAnalisisEmpresa extends Component {
    constructor(props) {
        super(props);
        this.state = {
           loading: false,
           chartvisible:true,
           codmarca:0,
           desmarca:"",
           tblMarcas:[]
        }
    }

    componentDidMount() {
        this.getTblMarcas();
    }

    getDataMarca=(codmarca,desmarca)=>{
        this.setState({chartvisible:false,codmarca:codmarca,desmarca:desmarca});
    }

    getTblMarcas=()=>{
        this.setState({"loading":true});

        axios.post(`${api}/getmarcas`,{
            empnit:this.props.empnit,
            anio:2021,
            mes:9
        })
        .then(res => {
          const marcas = res.data.recordset;
          //console.log(empresas);
          this.setState({"tblMarcas": marcas,"loading":false});
          //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"tblMarcas": [],"loading":false});
        })
    }

    setBack =() => {
        this.props.vista("INICIO");
        //this.props.empnit
    }

    render() {
       
        const dataM = this.state.tblMarcas;

            if(this.state.chartvisible==true){
                return(
                    <div>
                        <div className="row">
                            <div className="card shadow border-top-rounded border-bottom-rounded col-sm-12 col-md-6 col-xl-6 col-lg-6">
                                <div className="card-header">
                                    <h3>Ventas por Marca</h3>
                                </div>
                                <div className="card-body">
            
                                    <PieChart width={600} height={500}>
                                        <Pie
                                            dataKey="TOTALPRECIO"
                                            onClick={(e)=>{this.getDataMarca(e.CODMARCA,e.DESMARCA)}}
                                            isAnimationActive={true}
                                            data={dataM}
                                            cx={200}
                                            cy={200}
                                            outerRadius={200}
                                            fill="#8884d8"
                                            label
                                        >
                                            {dataM.map((entry, index) => (
                                                <Cell key={`cell-${index}`} fill="#8884d8" label={entry.DESMARCA} />
                                            ))}
                                            <LabelList dataKey="DESMARCA" position="inside" />
                                        </Pie>
                                    </PieChart>
            
                                </div>
                            </div> 
                            <div className="card shadow border-top-rounded border-bottom-rounded col-sm-12 col-md-6 col-xl-6 col-lg-6">
                                <div className="table-responsive">
                                    <table className="table table-hover ">
                                        <thead className="bg-secondary text-white ">
                                            <tr>
                                                <td>Marca</td>
                                                <td>Importe</td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {dataM.map((r)=>{
                                                return(
                                                    <tr>
                                                        <td>{r.DESMARCA}</td>
                                                        <td>{r.TOTALPRECIO}</td>
                                                    </tr>
                                                )
                                            })}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div className="">
                            <ButtonHome vista={this.setBack} />    
                        </div>
                        
                    </div>
                    )
            }else{
                return(
                    <div>
                        <div className="card shadow border-top-rounder">
                            <div className="card-header bg-info text-white">
                                <h3>Detalle de la Marca {this.state.desmarca}</h3>
                            </div>
                            <div className="card-body">
                                <button className="btn btn-primary" onClick={()=>{this.setState({chartvisible:true})}}>
                                    Regresar
                                </button>
                            </div>
                        </div> 
                        <ButtonHome vista={this.setBack} />
                    </div>
                    )
            }
   
    }
}